import React from 'react';
import styled from 'styled-components';

const StyledHueBox = styled.div`
  height: 30px;
  width: 30px;
  margin: auto;
  border-radius: 5px;
  background-color: hsl(${(props) => props.hue}, ${(props) => props.lightness});
  display: flex;
  & .letterBox {
    margin: auto;
  }
`;
const StyledFontBox = styled.div`
  height: 30px;
  width: 30px;
  margin: auto;
  border-radius: 5px;
  background-color: ${(props) => props.theme.colors.primaryColor};
  color: ${(props) => props.theme.colors.secondaryColor};
  display: flex;
  & .letterBox {
    margin: auto;
  }
`;
const SelectionBox = ({
  children,
  hue,
  lightness,
  font,
  weight,
  type,
  selectedBox,
  handleClick,
  id,
}) => {
  return type === 'hue' ? (
    <StyledHueBox
      selectedBox={selectedBox}
      hue={hue}
      lightness={lightness}
      onClick={() => {
        handleClick(id, type);
      }}
    >
      {children}
    </StyledHueBox>
  ) : (
    <StyledFontBox
      selectedBox={selectedBox}
      font={font}
      weight={weight}
      onClick={() => {
        handleClick(id, type);
      }}
    >
      {children}
    </StyledFontBox>
  );
};

export default SelectionBox;
